import {
    REQUEST_POSTS,
    RECEIVE_POSTS,
    FROM_REST
} from '../actions/organization'

export function resetFields(state = true, action) {
    switch (action.type) {
        case FROM_REST:
            return action.rest
        default:
            return state
    }
}


//combineReducers  
export function postsByTrees(state = {}, action) {
    switch (action.type) {
        case REQUEST_POSTS:
            // console.log(0)
        case RECEIVE_POSTS:
            // console.log(1)
            return Object.assign({}, state, {
                [action.name]: posts(state[action.name], action)
            })
        default:
            return state
    }
}

// posts  state
//  postsByTrees  state  posts 
// posts  reducer 
//  Redux 
function posts(state = {
    isFetching: false,
    didInvalidate: false,
    data: []
}, action) {
    switch (action.type) {
        case REQUEST_POSTS:
            // console.log(2)
            return Object.assign({}, state, {
                isFetching: true,
                didInvalidate: false
            })
        case RECEIVE_POSTS:
            // console.log(3)
            return Object.assign({}, state, {
                isFetching: false,
                didInvalidate: true,
                data: action.posts,
                lastUpdated: action.receivedAt
            })
        default:
            return state
    }
}

/**
 *  reducer  Array.prototype.reduce(reducer, ?initialValue) 
 */



