import {
    addTodo,
    toggleTodo,
    setVisibilityFilter
    // VisibilityFilters  //
} from '../actions/todo'

// selector  Redux store state  App
// selector  props App
import { getVisibleTodos } from '../selectors/todo'

import AddTodo from '../components/AddTodo.jsx'
import TodoList from '../components/TodoList.jsx'
import Footer from '../components/Footer.jsx'

class App extends Component {
    render() {
        //  connect() :
        const {dispatch, visibleTodos, visibilityFilter, length} = this.props
        return (
            <div>
                <AddTodo index={length} onAddClick={(text,count) => dispatch(addTodo(text, count))}/>
                <TodoList todos={visibleTodos} onTodoClick={index => dispatch(toggleTodo(index))}/>
                <Footer filter={visibilityFilter} onFilterChange={nextFilter => dispatch(setVisibilityFilter(nextFilter))}/>
            </div>
        )
    }
}

App.propTypes = {
    visibleTodos: PropTypes.arrayOf(PropTypes.shape({text: PropTypes.string.isRequired, completed: PropTypes.bool.isRequired}).isRequired).isRequired,
    visibilityFilter: PropTypes.oneOf(['SHOW_ALL', 'SHOW_COMPLETED', 'SHOW_ACTIVE']).isRequired
}

// 
// const getVisibleTodos = (todos, filter) => {
//     switch (filter) {
//         case VisibilityFilters.SHOW_ALL:
//             return todos
//         case VisibilityFilters.SHOW_COMPLETED:
//             return todos.filter(todo => todo.completed)
//         case VisibilityFilters.SHOW_ACTIVE:
//             return todos.filter(todo => !todo.completed)
//     }
// }

//
//stateprops
//state UI  UI 
const mapStateToProps = (state) => {
    return {
        visibleTodos: getVisibleTodos(state),
        visibilityFilter: state.visibilityFilter,
        length: state.todos.length
    }
}

//  component  dispatch  state  connect(select)(App) 
// connect()  selector
//  Redux store  state
//  props
//  state 
// 
//  connect  mapStateToProps  mapStateToProps 
export default connect(
    mapStateToProps
)(App)



